import './Cards.scss';
import Card from "./Card";

const Cards = () => {
    return(
        <div className='card--container'>
            <div className='card--container__left'>
                <img className='card--img' src={require('../images/red_c.jpg')} />
            </div>
            <div className='card--container__right'>
                <Card text={'El cuerpo como primer instrumento'} />
                <Card text={'La bsqueda de un sonido propio en la interpretacin y composicin'} />
                <Card text={'La tcnica y la teora aplicada en la msica que te gusta'} />
                <Card text={'Te acompaamos durante todo el proceso, cualquiera sea tu nivel'}/>
            </div>
        </div>
    );
}

export default Cards;